import React from 'react';
import ReactDOM from 'react-dom';
import RuleConditionModal from '../components/RuleConditionModal/RuleConditionModal';

const useRuleConditionModal = () => {

    const openRuleConditionModal = (ruleId) => {
        ReactDOM.render(
            ReactDOM.createPortal(
                <RuleConditionModal ruleId={ruleId} />,
                document.querySelector("#extension-app")),
            document.createElement('div')
        );
    };

    return { openRuleConditionModal };
};

export default useRuleConditionModal;
